import { Pipe, PipeTransform } from '@angular/core';
import { Commander } from '../../classes/commander';

@Pipe({
    name: 'commanderFilter',
    pure: false
})
export class CommanderFilterPipe implements PipeTransform {
  transform(items: Commander[], filter: Commander): Commander[] {
    if (!items || !filter) {
      return items;
    }
    // filter items array, items which match and return true will be kept, false will be filtered out
    return items.filter((item: Commander) => this.applyFilter(item, filter));
  }


  applyFilter(book: Commander, filter: Commander): boolean {
    for (let field in filter) {
      if (filter[field]) {
        if (typeof filter[field] === 'string') {
          if (book[field].toLowerCase().indexOf(filter[field].toLowerCase()) === -1) {
            return false;
          }
        } else if (typeof filter[field] === 'number') {
          if (book[field] !== filter[field]) {
            return false;
          }
        }
      }
    }
    return true;
  }
}



